html{
  --res: calc(0.01 * 10vmin);
}
.header{
  .header-slide{
    height:95vh;
    overflow:hidden;
    display:flex;
    justify-content:center;
    align-items: center;

    .pexel{
      width:99vw;
      height:95vh;
      object-fit: cover;
      background-position: center;
    }

    &::before{
      content:'';
      position:absolute;
      left:0;
      top:0;
      width: 100%;
      height:100%;
      background-color: rgba($color:#000000, $alpha:0.6 );
    }
      
    .parent-presentation{
      position:absolute;
      z-index:1;
      display:flex;
      justify-content:center;
      align-items: center;
      width:90vw;
      height:70vh;
      margin:0 auto;

      .presentation1{

        display:flex;
        flex-direction: column;
        justify-content:center;
        align-items:flex-start;
        flex-wrap: wrap;
        color:#fff;
        //width:50vw;
        //height: 38vh;
        
        h1{
          text-align: start;
          font-size:calc(120 * var(--res));
          //white-space: nowrap;
          margin-top: -35px;
          font-weight:500;
          color:transparent;
          -webkit-background-clip: text;
          -moz-background-clip: text;
          -ms-background-clip: text;
          -o-background-clip: text;
          background-clip: text;
          background-image: radial-gradient(circle at 43% 39%, rgba(0,0,0,0.02) 0%, rgba(0,0,0,0.02) 2%,transparent 2%, transparent 100%),radial-gradient(circle at 71% 44%, rgba(0,0,0,0.02) 0%, rgba(0,0,0,0.02) 2%,transparent 2%, transparent 100%),radial-gradient(circle at 50% 74%, rgba(0,0,0,0.03) 0%, rgba(0,0,0,0.03) 8%,transparent 8%, transparent 100%),radial-gradient(circle at 34% 20%, rgba(0,0,0,0.03) 0%, rgba(0,0,0,0.03) 8%,transparent 8%, transparent 100%),radial-gradient(circle at 44% 59%, rgba(0,0,0,0.01) 0%, rgba(0,0,0,0.01) 8%,transparent 8%, transparent 100%),radial-gradient(circle at 6% 48%, rgba(0,0,0,0.02) 0%, rgba(0,0,0,0.02) 5%,transparent 5%, transparent 100%),radial-gradient(circle at 94% 15%, rgba(0,0,0,0.03) 0%, rgba(0,0,0,0.03) 5%,transparent 5%, transparent 100%),radial-gradient(circle at 81% 40%, rgba(0,0,0,0.03) 0%, rgba(0,0,0,0.03) 5%,transparent 5%, transparent 100%),radial-gradient(circle at 7% 68%, rgba(0,0,0,0.03) 0%, rgba(0,0,0,0.03) 5%,transparent 5%, transparent 100%),radial-gradient(circle at 10% 37%, rgba(0,0,0,0.01) 0%, rgba(0,0,0,0.01) 5%,transparent 5%, transparent 100%),linear-gradient(321deg,rgb(254,254,5),rgb(254,63,3));

        }
        p{
          text-align: start;
          margin-top: 13px;
          font-weight: 500;
          margin-top: 15px;
        }
        .header_btn{
          display:flex;
          justify-content: center;
          align-items: center;
          margin-top: 30px;
          margin-left: 1vw;

          button{
            position:relative;
            margin-right:3vw;
            padding: 2px 15px 5px;
            font-size: calc(44 *var(--res));
            text-align: start;
            border-radius: 25px;
            background: transparent;
            border: 2px solid #CE6A14;
            cursor:pointer;
            color:#fff;
            overflow:hidden;

            &::before{
              position:absolute;
              content:'';
              top:0;
              left:50%;
              width:0%;
              height: 100%;
              background-image: radial-gradient(circle at 43% 39%, rgba(0,0,0,0.02) 0%, rgba(0,0,0,0.02) 2%,transparent 2%, transparent 100%),radial-gradient(circle at 71% 44%, rgba(0,0,0,0.02) 0%, rgba(0,0,0,0.02) 2%,transparent 2%, transparent 100%),radial-gradient(circle at 50% 74%, rgba(0,0,0,0.03) 0%, rgba(0,0,0,0.03) 8%,transparent 8%, transparent 100%),radial-gradient(circle at 34% 20%, rgba(0,0,0,0.03) 0%, rgba(0,0,0,0.03) 8%,transparent 8%, transparent 100%),radial-gradient(circle at 44% 59%, rgba(0,0,0,0.01) 0%, rgba(0,0,0,0.01) 8%,transparent 8%, transparent 100%),radial-gradient(circle at 6% 48%, rgba(0,0,0,0.02) 0%, rgba(0,0,0,0.02) 5%,transparent 5%, transparent 100%),radial-gradient(circle at 94% 15%, rgba(0,0,0,0.03) 0%, rgba(0,0,0,0.03) 5%,transparent 5%, transparent 100%),radial-gradient(circle at 81% 40%, rgba(0,0,0,0.03) 0%, rgba(0,0,0,0.03) 5%,transparent 5%, transparent 100%),radial-gradient(circle at 7% 68%, rgba(0,0,0,0.03) 0%, rgba(0,0,0,0.03) 5%,transparent 5%, transparent 100%),radial-gradient(circle at 10% 37%, rgba(0,0,0,0.01) 0%, rgba(0,0,0,0.01) 5%,transparent 5%, transparent 100%),linear-gradient(321deg,rgb(254,254,5),rgb(254,63,3));
              //background:#CE6A14;
              border-radius:5px;
              transform:translateX(-50%);
              transition:width 0.5s ease-out;
              z-index:-1;
            }
            &:hover::before{
              width:110%;
            }
            // &:hover{
            //   border:none;
            // }
          }
        }
        h1,
        p,
        .header_btn{
          opacity:0;
          transform:translateY(-100px);
          transition : transform 0.7s ease, opacity 0.7s ease ;
        }
      }
      //.header_img{
        // display:flex;
        // justify-content:center;
        // height: 290px;
        // width: 800px;
        
        .img{
          height: 29vw;
          width: 30vw;
          object-fit: cover;
          border-radius: 15px;
          margin-left:20px;
          transform:scale(0);
          transition:transform 1s ease-out;
        }
        // .shoes_text{
        //   display:none;
        // }
      //}
      
    }

    &.active > .parent-presentation > .img{
    transform:scale(1);
    }
    &.active > .parent-presentation > .presentation1{
      h1,
      p,
      .header_btn{
        transform: translateY(0px);
        opacity: 1;
      }
      h1{
        transition-delay: 0.6s, 0.6s;
      }
      p{
        transition-delay: 0.9s, 0.9s;
      }
      .header_btn{
        transition-delay: 1.2s, 1.2s;
      }
    }
  }
}
  
  
//RESPONSIVE

@media screen and (max-width:810px) {
  .header .header-slide .parent-presentation{
    width:80vw;
    .presentation1 h1{
      position:absolute;
      text-align: center;
      top:0;
    }
    .presentation1 .header_btn{
      position:absolute;
      bottom:0;
      width:100%;
      flex-direction: column;
      margin:0 auto;
      button{
        margin:10px;
      }
      
    }
  } 
}
@media screen and (max-width:400px) {
  .header .header-slide .parent-presentation .presentation1 .header_btn{
    margin-left:5vw;
  }
}